.Portfolio {
    position: relative;
}
.Portfolio .box {
    /*height: 100%;*/
    border-radius: 10px;
    position: relative;
}
/*.Portfolio .box:hover {*/
/*    background: white;*/
/*    transform: translateY(-0);*/
/*}*/

/*.Portfolio .box:hover {*/
/*    background-color: #b5dee7;*/
/*}*/

.Portfolio .img {
    height: 300px;
    overflow: hidden;
    transition: 0.5s;
}
.Portfolio img {
    width: 100%;
    /*height: 100%;*/
    border-radius: 10px;
    transition: 0.5s;
}
.Portfolio .left {
    /*min-width: 100%;*/
    margin-right: 5%;
    width: 30%
}
.Portfolio .right {
    /*margin-right: 5%;*/
    width: 70%;
    /*min-width: 100%;*/
    position: relative;
}
.Portfolio .box .img:hover img {
    transform: scale(1.05);
}
.Portfolio .category {
    margin: 20px 0 30px 0;
    text-transform: uppercase;
}
.Portfolio .category span {
    color: #ff014f;
    font-size: 13px;
    letter-spacing: 2px;
    font-weight: 500;
}


.Portfolio .arrow {
    position: absolute;
    right: 30px;
    bottom: 11%;
    transform: rotate(-45deg);
    color: #ff014f;
    opacity: 0;
    transition: 0.5s;
}
.Portfolio .title:hover .arrow {
    opacity: 1;
}
.Portfolio h2 {
    /*color: white;*/
    font-size: 18px;
    transition: 0.5s;
    padding-bottom: 30px;
}
.Portfolio .box:hover h2 {
    color: white;
}
.Portfolio .box:hover span {
    color: #3fcdf1;
}
.Portfolio .box:hover p {
    color: floralwhite;
}
.Portfolio .box h3 {
    color: #7cc9c0;
}

.Portfolio .box:hover h3{
    text-decoration: underline;
    color: #e089de;

}
.Portfolio .box h3:hover {
    text-underline-mode: true;
    color: #e089de;
    transform: scale(1.2);
}
.Portfolio .box:hover {
    /*color: black;*/
    background-color: #10343d;
    transition: 0.5s;
}
/*------------popup--------*/
body.active-modal {
    overflow-y: hidden;
}

.btn-modal {
    padding: 10px 10px;
    display: block;
    margin: 100px auto 0;
    font-size: 18px;
}

.modal,
.overlay {
    overflow: scroll;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    z-index: 9999;
}

.overlay {
    background-color: #ecf0f3;
}
.modal-content {
    position: absolute;
    top: 12%;
    left: 8%;
    line-height: 1.4;
    background: #f0f3f5;
    padding: 14px 28px;
    border-radius: 3px;
    max-width: 85%;
    min-width: 60%;
    z-index: 9999;
    box-shadow: 4px 4px 8px #cbcbcb, -4px -4px 8px #ffffff;
    padding: 50px;
    border-radius: 10px;
}

.modal-text {
    padding: 0 100px 0 50px;
}
.modal-text h1 {
    margin: 20px 0 20px 0;
    font-size: 30px;
}
.modal-text p {
    font-size: 17px;
    margin-bottom: 20px;
    line-height: 25px;
}
.modal-text button {
    margin-right: 20px;
    color: #ff014f;
}
.modal-text button i {
    margin-left: 10px;
}
.modal-text button.close-modal {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 50px;
    height: 50px;
    text-align: center;
    border-radius: 50%;
    padding: 0 0;
    font-size: 20px;
}
.modal-text button.close-modal i {
    margin: 0;
}
/*------------popup--------*/
@media (max-width: 768px) {
    .modal,
    .overlay {
        overflow: scroll;
    }

    .modal .modal-img {
        margin: 50px 0 50px 0;
        height: 100%;
    }
    .grid {
        padding: 10px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 35px;
    }
    .Portfolio .img {
        height: 130px;
        overflow: hidden;
        transition: 0.5s;
    }
    .modal .right,
    .modal .left {
        padding: 0;
    }
    .modal-text {
        padding: 0 10px 0 10px;
    }
}